    import articlePages from '../views/articlePages.vue'

const pages = [
    {
        path: '/',
        redirect: '/welcome',
    },
    {
        path: '/main',
        name: 'main',
        component: main,
        children: [
            {
                path: '/welcome',
                name: 'welcome',
                meta: {
                    title: 'welcome'
                },
                component: welcome,
            },
            {
                path: '/articlePages',
                name: 'articlePages',
                meta: {
                    title: 'articleContainer'
                },
                component: articlePages,
            }
        ]
    }
]

export default pages;